.company {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background: white; }

.el-company {
  font-family: "Circe";
  flex: 0 0 250px;
  font-size: 16px;
  line-height: 160%;
  color: black; }
  .el-company:nth-child(1), .el-company:nth-child(2) {
    margin-bottom: 70px; }
  .el-company__img {
    margin-bottom: 28px; }
    .el-company__img img {
      max-width: 90px;
      max-height: 72px; }
  .el-company__title {
    font-weight: 700;
    line-height: 1em;
    margin-bottom: 25px; }
  .el-company__desc {
    max-width: 240px; }
